<template>
  <div class="hello">
    <el-table
      :data="tableData"
      style="width: 100%">
      <el-table-column
        prop="name"
        label="标题"
        header-align="center">
      </el-table-column>
      <el-table-column
        prop="url"
        label="链接"
        header-align="center">
      </el-table-column>
      <el-table-column
        prop="type"
        label="网站类型"
        header-align="center">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>

export default {
  name: 'MirrorUrls',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      columns_map: {
        url: '链接',
        name: '名称'
      },
      tableData: []
    }
  },
  mounted () {
    this.readmirrorfile()
  },
  methods: {
    readmirrorfile: function () {
      var data = require('../files/mirrorurls')
      this.tableData = data.mirrors
      console.log(data)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



