import Vue from 'vue'
import Router from 'vue-router'
import MirrorUrls from '@/components/Mirrors'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: MirrorUrls
    },
    {
      path: '/mirrors',
      name: 'Mirrors',
      component: MirrorUrls
    }
  ]
})



